.MenuBar
{
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: fixed;

    padding: 20px 0px;
    background-color: white;
    z-index: 100;
}

#menu-item-container
{
    width: 50%;
    display: flex;
    justify-content: flex-end;
}

.MenuItem,
.site-logo
{
   
    margin: auto 0px;
}

.MenuItem:hover,
.site-logo:hover
{
    cursor: pointer;
}

.MenuItem:hover
{
    opacity: .5;
}

.site-logo
{
    padding: 0px 30px;
    font-size: 30px;
    font-weight: 700;
}

.MenuItem
{
    text-decoration: none;
    color: black;
    display: inline-block;
    width: calc(50vw / 3);
    text-align: center;
    padding: 0px 10px;
}

.site-logo div
{
    display: inline-block;
}

.rest
{
    letter-spacing: -12px;
    margin-left: -10px;
    opacity: 0;
}