.ContentPage
{
    min-height: calc(100vh - 76px - 50px); /*subtract menu height and landing box padding*/
    display: flex;
    justify-content: center;
}

.ContentItem
{
    width: calc(100vw / 3);
    padding: 0px 6vw;
}

.content-icon
{
    width: 35%;
    max-width: 150px;

    background-color: black;
    color: white;

    font-weight: 700;
    font-size: calc(4vw);

    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes highlightIcon {
    10% {
        color: white;
        background-color: black;
    }
    30% {
        color: black;
        background-color: white;
    }
    100% {
        color: white;
        background-color: black;
    }
  }

.active .content-icon
{
    animation-name: highlightIcon;
    animation-duration: 2s;
}

.content-icon:before 
{
	content: "";
	float: left;
	padding-top: 100%;
}

.ContentItem h1
{
    font-size: 40px;
    margin-bottom: 10px;

}

.ContentItem p
{
    margin-bottom: 60px;
}