.LandingPage
{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    /* Hide scrollbar for IE, Edge and Firefox */
    -ms-overflow-style: initial;  /* IE and Edge */
    scrollbar-width: initial;  /* Firefox */
}

.bordered-box::-webkit-scrollbar 
{
    display: initial;
}

.landing-title
{
    text-transform: capitalize;
}

.LandingBox
{
    width: 50vw;
    padding: 50px;
    display: flex;
    justify-content: center;

    background-color: white;
}

.LandingImage
{
    width: 40%;
    
    margin: auto 0px;
    margin-right: -20%;
    z-index: 10;

    background-color: black;
    color: white;

    font-weight: 700;
    font-size: calc(50vw * .2);

    display: flex;
    justify-content: center;
    align-items: center;
}

.LandingImage:before 
{
	content: "";
	float: left;
	padding-top: 100%;
}

.bordered-box
{
    width: 80%;
    border: 1px solid black;
    display: flex;
    justify-content: flex-end;
    padding: 8vh;
    max-height: 60vh;
    overflow-x: hidden;
    overflow-y: scroll;
}

.landing-text-container
{
    width: 70%;
    margin: auto 0px;
}

.LandingBox h1
{
    font-size: 50px;
    margin: 10px 0px;
    overflow-x: hidden;
    overflow-y: hidden;
    text-overflow: ellipsis;
}

.LandingBox p
{
    font-size: 16px;
    line-height: 1.3;
    font-weight: 300;
}

.LandingBox button
{
    background-color: transparent;
    border: 1px solid black;
    padding: 8px 30px;
    font-weight: 100;
    font-size: 14px;
}

.LandingBox button:hover
{
    cursor: pointer;
    opacity: 0.5;
}